import {useState} from 'react';
import AddTodo from './testAdd.js';

export default function Test() {
    const [messages, setmessages] = useState(["item 1", "item 2", "item 3"]);
  
    return (
      <div>
        <TodoList messages={messages} />
        <AddTodo setmessages={setmessages} />
      </div>
    );
  }
  
  
  function TodoList({ messages }) {
    return (
      <ul>
        {messages.map((todo) => (
          <li key={todo}>{todo}</li>
        ))}
      </ul>
    );
  }