import { VariableSizeList as List } from 'react-window';

const exampleChatlog = [
  ['Sotaro', 'I like this box'],
  ['Kaneda', 'Yes, it\'s quite cozy']
]

const messages = Array(100).fill(...exampleChatlog)

function Row(props) {
  const message = props.data[props.index];
  return (
    <div style={props.style}> {message[0]  + ': ' + message[1]} </div>
  );
};

function Chatlog(props) {
  return (  
    <List
      height={100}
      itemCount={100}
      itemSize={()=>50}
      width={300}
      itemData={messages}
    >
      {Row}
    </List>
  );
}

export default Chatlog;